.navbar {
    width: 100%;
    top: 0;
    position: absolute;
    height: 90px;
    background:linear-gradient(20deg, #080334, #770666);
    display: flex;
    justify-content: space-between;
    align-items: center;
}



.navbar-logo {
    height: 83px;
    margin: 5px;
    display: flex;
}



.links {
    max-height: 90px;
    flex: 50% 1;
    list-style: none;
    display: flex;
}

.links:hover {
    background-color: #FE4693;
}



.navbar a {
    color: white;
    text-decoration: none;
    margin: 20px;
    font-size: 25px;
}


.toggleButton {
    font-size: 100px;    
}


.toggleButton button {
    margin-right: 20px;
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
}


#open {
    height: 100vh;
}


#open .links {
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 100px;
}

#open a {
    width: 100%;
    text-align: center;
}

@media only screen and (max-width: 900px) {
    .navbar a {
        width: 70px;        
    }
}

@media only screen and (max-width: 600px) {
    .toggleButton {
        display: flex;
    }
    #close a {
        display:none;
    }
}

@media only screen and (min-width:600px) {
    .toggleButton {
        display: none;
    }
}

.hiddenLinks {
    display: none;
}

#open {
    height: 100px;
}

#open .links {
    flex-direction: row;
}





    



.footer {
    position: fixed;
   left: 0;
   bottom: 0;
    width: 100%;
    height: 90px;   
    background: #6a025a;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 10px;
    margin-top: 200px; 
}


.socialMedia svg {
    color: white;
    margin: 15px;
    margin-bottom: 1px;    
    font-size: 45px;
    cursor: pointer;
    transition:all 0.5s linear;   
}


.socialMedia svg:hover {
    background-color: #FE4693;
}



.footer p {
    color: white;   
}



@media only screen and (max-width: 600px) {

    .footer svg {
        font-size: 50px;
    }
}
