
.footer {
    position: fixed;
   left: 0;
   bottom: 0;
    width: 100%;
    height: 90px;   
    background: #6a025a;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 10px;
    margin-top: 200px; 
}


.socialMedia svg {
    color: white;
    margin: 15px;
    margin-bottom: 1px;    
    font-size: 45px;
    cursor: pointer;
    transition:all 0.5s linear;   
}


.socialMedia svg:hover {
    background-color: #FE4693;
}



.footer p {
    color: white;   
}



@media only screen and (max-width: 600px) {

    .footer svg {
        font-size: 50px;
    }
}