


.container {
    background: linear-gradient(33deg, rgba(242,242,255,1) 0%, rgba(235, 249, 255, 1) 100%);
    padding-top:  8.8rem;
    height: 20rem;    
}


.home-about {
    width: 100%;
    background-color: #fff;
    padding: 5px 0;
    margin-bottom: -12px;
}

.home-content  { 
    font-size: 23px;    
    color: black;       
    margin: 45px;
    text-align: left;
    margin-left: 400px;
    line-height: 32px;
}




.about-title h2 {
    width: 100%;
    max-width:  20px auto;  
    text-align: center;
    line-height: 1.6; 
    color:#971561;
    font-size: 30px;   
    margin-bottom: -15px;   
    margin-top: -15px; 
}




@media only screen and (max-width: 768px) {
    
    .about-img {
        height: 100%   
     }
}



.about-content p {
    font-size: 23px;    
    color: black;       
    margin: 45px;
    text-align: left;
    margin-left: 400px;
    line-height: 32px;
}
